
.image-carousel-container, .main-container{
    width: 100%;
    height: 100%;
    height: -webkit-fill-available;
    display: flex;
    flex-wrap: nowrap;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    /* position: relative; */
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
}
.main-container{
    position: relative;
}

.image-carousel-container > .image{
    width: 100%;
    flex-shrink: 0;
    scroll-snap-align: start;
    display: flex;
    justify-content: center;
    overflow: hidden;
}


.carousel-button{
    position: absolute;
    display: block;
    top: 40%;
}
.carousel-button button{
    width: 30px;
    height: 50px;
    cursor: pointer;
}
.right{
    right: 0;
}
.left{
    left: 0;
}

/* slider dots */
.slider-dots-container{
    position: absolute;
    justify-self: center;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 10%;
}
.dot-link{
    position: relative;
    margin: 0 3px;
}
.slider-dot{
    position: absolute;
}

.slider-dot,.dot-link{
    display: inline-block;
    width: 6px;
    height: 6px;
    border-radius: 100%;
    -webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    -ms-border-radius: 100%;
    -o-border-radius: 100%;
    background-color: var(--dot-color);
    cursor: pointer;
    box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.4);
}

.dot-link:first-child > span{
    background-color: #fff;
}

@media screen and (max-width:1000px) {
    .image-carousel-container > div img{
        height: 40vmin;
    }

    .carousel-button{
        display: none;
    }
    .slider-dots-container{
        display: flex !important;
    }
}