.add-product-title{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: #222;
    padding-top: .5rem;
}

/* Labelinout component style */

.detail-section .input-container{
    gap: 0;
    width: 100%;
}

.detail-section .input-container > label{
    flex: .2 1;
}
.detail-section .input-container:nth-child(2){
    flex: 1;
}

/* detail section */
form{
    gap: .2rem;
    position: relative;
}
form .detail-section{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(var(--add-product-inputs), auto));
    /* place-items: center; */
    row-gap: .5rem;
    overflow: hidden;
}

input[name='priceSymbol']{
    display: none;
}
.price-symbol-section .price-symbol-container{
    flex-shrink: 0;
}
.price-symbol-section .price-symbol-container label{
    padding-block: .3rem;
    padding-inline: 1rem;
    border: 1px solid var(--border-light);
    box-shadow: 0 0 5px rgba(0, 0, 0, .4);
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    margin-inline: .5rem;
}
.price-symbol-section .price-symbol-container label > *{
    transform: translateY(3px);
    -webkit-transform: translateY(3px);
    -moz-transform: translateY(3px);
    -ms-transform: translateY(3px);
    -o-transform: translateY(3px);
}
.price-symbol-container label:hover{
    cursor: pointer;
    background-color: var(--border-light);
}
.price-symbol-container label.active{
    background-color: var(--primary);
    color: #fff;
    font-weight: bold;
}

input[name='priceSymbol']:checked + label{
    background-color: var(--primary);
    color: #fff;
    font-weight: bold;
}

input[name='priceSymbol']:not(:checked) + label{
    background-color: #fff;
    color: black;
}
/* keywords/categorys section */

.keywords-section label{
    flex: .2;
    text-align: right;
    transform: translateX(-6px);
    -webkit-transform: translateX(-6px);
    -moz-transform: translateX(-6px);
    -ms-transform: translateX(-6px);
    -o-transform: translateX(-6px);
}

textarea, #additional{
    width: 100%;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, .4);
    outline: none;
    border: none;
    line-height: 1.2rem;
    font-size: 1.2rem;
    font-family: sans-serif;
    text-indent: 8px;
    padding-block: .65rem;
}

textarea:focus, #additional:focus{
    box-shadow: 0 0 5px 2px var(--primary);
}

/* additional section */

.additional-info > label{
    flex: .2;
    text-align: center;
}

.additional-info .text-container .text-style-accessory{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.text-style-accessory > *{
    display: inline-block;
    padding: .1rem .5rem;
    box-shadow: 0 -1px 5px rgba(0, 0, 0, .4);
    border: 1px solid var(--border-light);
    cursor: pointer;
    font-family: monospace;
    font-size: 1.1rem;
    z-index: 99;
    background-color: #fff;
}
label[for='color-text']{
    padding: 0;
}
input[type='color']{
    cursor: pointer;
    width: 3.5vmax;
    aspect-ratio: 1;
    outline: 0;
    padding: 0;
    margin: 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, .4);
    border: 1px solid var(--border-light);
}
.text-container .text-style-accessory input[type='checkbox']{
    display: none;
}
input[name='style-text']:checked + label:not([for='color-text']){
    background-color: #333;
    color: #fff;
}

/* next button */
.next-btn-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1.8rem;
}
.next-btn-container .icon-next{
    transform: translateY(3px);
    -webkit-transform: translateY(3px);
    -moz-transform: translateY(3px);
    -ms-transform: translateY(3px);
    -o-transform: translateY(3px);
    font-weight: bold;
}


/* uplaod/selected images */
input[id="upload-images"]{
    display: none;
}
label[for='upload-images']{
    width: 100%;
    display: block;
    text-align: center;
}
label[for='upload-images']>*{
    cursor: pointer;
}

.upload-images-section{
    width: 100%;
    margin-top: 5vh;
    /* background-color: red; */
    /* display: flex;
    flex-direction: column; */
}
.seleceted-images-section{
    width: 100%;
    /* background-color: #222; */
    display: flex;
    /* flex-wrap: wrap; */
    justify-content: center;
    margin-block: 1rem;
    gap: 1rem;
    overflow: scroll;
    user-select: none;
    padding: 0 1.5rem;
}
.selected-image-container{
    position: relative;
    width: var(--product-card-size);
    min-height: 200px;
    border: 1px dashed rgb(106, 106, 106);
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
}
.selected-image-container img{
    width: 100%;
    object-fit: fill;
}
.selected-image-container img[src=''] + .close{
    display: none;
}

.selected-image-container  .close{
    color: #333;
}
.selected-image-container .close:hover{
    color: #222;
}

.add-product-btn-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2.5rem 0;
    gap: 1rem;
}


/* media query */
@media screen and (max-width: 900px){
    :root{
        --add-product-inputs: 350px;
    }
}

@media screen and (max-width: 650px) {
    :root{
        --add-product-inputs: 300px;
    }
    .seleceted-images-section{
        justify-content: flex-start;
    }
    form .detail-section{
        place-items: center;
    }
    .next-btn-container button{
        margin-bottom: 2.5rem;
    }
    
}


@media screen and (max-width: 650px) {
    .selected-image-container{
        flex-direction: column;
    }
    /* .selected-image-container{
        min-width: 80vmin;
    } */
}


/*  */
.bold{
    font-weight: bold;
}