:root{
    --seller-logo-color: brightness(50%) sepia(100) saturate(45) hue-rotate(218deg);
}
.container{
    width: 100%;
    height: 100vh;
    overflow: scroll;
    background-color: var(--border-light);
}
.wraper{
    display: flex;
}
.wraper > *{
    background-color: #fff;
    height: 100vh;
}
.wraper .layout-container{
    flex-basis: 15%;
    flex-grow: 0;
    border-right: 1px solid var(--border-light);
    background-color: rgba(255, 255, 255, 0.631);
    position: relative;
    transition: all .1s;
    -webkit-transition: all .1s;
    -moz-transition: all .1s;
    -ms-transition: all .1s;
    -o-transition: all .1s;
}
.wraper .outlet-container{
    flex-basis: 1;
    flex-grow: 1;
    overflow: scroll;
}

/* layout container childs */
.toggle-container{
    display: flex;
    justify-content: flex-end;
    padding-top: .2rem;
}
.fold-layout{
    width: 5vmin;
    background-color: #fff;
    padding: .1rem .35rem;
    box-shadow: -2px 0 5px 1px rgba(0, 0, 0, 0.31);
    border-top-left-radius: .5rem;
    border-bottom-left-radius: .5rem;
}
.fold-layout label span:last-child{
    display: none;
}
.fold-layout:hover, .fold-layout label:hover{
    cursor: pointer;
}

input[data='toggle']{
    position: absolute;
    visibility: hidden;
    z-index: -1;
}
input[name="toggle-layout"]:checked ~ .layout-container .fold-layout label span:first-child{
    display: none;
}
input[name="toggle-layout"]:checked ~ .layout-container .fold-layout label span:last-child{
    display: block;
}
input[name="toggle-layout"]:checked ~  .layout-container .menu-options p{
    display: none;
}
input[name="toggle-layout"]:checked ~ .layout-container .logo{
    text-align: center;
    padding: 0;
}
input[name="toggle-layout"]:checked ~ .layout-container .logo img{
    max-width: 70px;
}
input[name="toggle-layout"]:checked ~ .layout-container{
    flex-basis: 8%;
}

.logo{
    text-align: left;
    flex-basis: 10%;
    margin-top: .4rem;
    padding-left: 1rem;
    margin-bottom: 1rem;
}
.logo img{
    width: 75%;
    max-width: 90px;
    filter: var(--seller-logo-color);
    -webkit-filter: var(--seller-logo-color);
}
.menu-options{
    height: auto;
    flex: 1;
    opacity: .8;
    padding: .5rem;
    border-top-right-radius: .5rem;
    border-bottom-right-radius: .5rem;
    margin: .2rem 0;
}
.menu-options:hover{
    background-color: var(--border-light);

}
.menu-options a{
    text-decoration: none;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: .95rem;
    display: flex;
    justify-content: center;
    gap: .5rem;
}

.outline-icon{
    display: block;
}

.menu-options.active{
    background-color: var(--primary);
}
.menu-options.active a {
    color: #fff;
}
.menu-options.active a .outline-icon{
    display: none;
}
.menu-options:not(.active) > a .fill-icon{
    display: none;
}
.menu-drop-icon{
    display: none;
}

@media screen and (max-width: 950px) {
    :root{
        --sellerlogo-color: brightness(50%) sepia(100) saturate(25) hue-rotate(3deg);
    }
    .wraper{
        flex-direction: column;
    }
    .wraper .layout-container{
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-direction: row;
        border: none;
        border-bottom: 1px solid var(--border-light);
    }
    .logo{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        margin: 0;
    }
    .logo .logo-img{
        text-align: left;
        padding-left: 1.5rem;
        display: flex;
        align-items: center;
    }
    hr{
        display: none;
    }
    .logo img{
        width: 45%;
        max-width: 350px;
    }
    .toggle-container{
        display: none;
    }
    .layout-container > *{
        flex: 1;
    }
    .menu-options.active{
        background-color: transparent !important;
        text-decoration: underline;
    }
    .menu-options:hover{
        background-color: transparent;
    }
    .menu-options.active a{
        color: var(--primary);
        opacity: 1;
    }
    .menu-options a{
        justify-content: center;
        gap: .4rem;
        width: max-content;
        opacity: .7;
    }
    .menu-options a:hover{
        opacity: 1;
    }
}

@media screen and (max-width: 650px){
    :root{
        --seller-logo-color: brightness(50%) sepia(100) saturate(10) hue-rotate(280deg);
    }
    .menu-drop-icon{
        display: block;
        flex: 0.1;
        text-align: center;
        color: var(--primary);
    }
    .menu-drop-icon>*:hover{
        cursor: pointer;
        color: rgb(158, 157, 157);
    }

    .wraper .layout-container{
        flex-direction: column;
        align-items: center;  
        justify-content: flex-start;
        display: block;
        padding-top: .3rem;
    }

    .logo{
        width: 100%;
        padding: .45rem;
    }
    .logo .logo-img{
        flex: 1;
        justify-content: center;
        padding: 0;
    }
    .logo .logo-img img{
        width: 26%;
        max-width: 75px;
    }
    .menu-options{
        padding: 0;
        margin: 0;
        overflow: hidden;
        height: 0vh;
        transition: all .25s;
        -webkit-transition: all .25s;
        -moz-transition: all .25s;
        -ms-transition: all .25s;
        -o-transition: all .25s;
}

    input[name="toggle-drop-layout"]:checked ~ .layout-container .menu-options{
        height: auto;
        padding: .5rem;
        margin: .2rem;
    }

    input[name="toggle-drop-layout"]:checked ~ .layout-container .logo .menu-drop-icon>*{
        color: rgb(117, 117, 117) !important;
    }
    .hide-icon{
        visibility: hidden;
    }
}