.entry-container{
    width: 100%;
    height: 100vh;
    background-color: var(--border-light);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.wraper{
    max-width: 550px;
    width: 100%;
    height: 100vh;
    overflow: scroll;
    padding: .5rem;
}

.seller-description{
    padding-block-end: 1.7rem;
}

.seller-description h1{
    color: var(--primary);
}

.seller-description ul{
    margin-block-start: 0;
    padding-inline-start: 1.5rem;
    font-size: .95rem;
}

.entry-card-container{
    position: relative;
    width: 100%;
    min-height: 300px;
    max-height: auto;
    background-color: #fff;
    border-radius: 1px;
    -webkit-border-radius: 1px;
    -moz-border-radius: 1px;
    -ms-border-radius: 1px;
    -o-border-radius: 1px;
    box-shadow: 0 0 8px 1px rgba(43, 43, 43, 0.3);
}
.title-container{
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    padding-top: .95rem;
}

.link-btn{
    text-decoration: none;
    color: black;
}
.link-btn:hover{
    text-decoration: underline;
}

/* Seller Signup */
.seller-signup-container{
    padding-block: 1rem;
}
form{
    align-items: center;
    gap: 1rem;
}
.otp-btn-contain{
    margin  : 0 !important;
    padding : 0 !important;
}
