
.product-card{
    display: flex;
    flex-direction: column;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    justify-content: center;
    background-color: #fff;
    box-shadow: 0 0 7px 1px rgba(0,0,0,.35);
    width: var(--product-card-size);
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    padding-bottom: .5rem;
    float: left;
    position: relative;
    align-self: stretch;
}
.product-card .image-carousel{
    width: 100%;
    aspect-ratio: 1;
    display: flex;
    align-items: center;
    margin-bottom: 2px;
    overflow: hidden;
    position: relative;
}

.product-details{
    padding: 0 1rem;
}

.product-card .name{
    text-transform: capitalize;
    color: var(--product-name);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: .9rem;
}
.description{
    font-size: .8rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: .2rem;
}
.price-container{
    color: var(--price-color);
    display: flex;
    margin-bottom: .2rem;
    align-items: center;
}
.price-container > div{
    margin: 0 .7rem;
}
.discount-contain{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    font-size: .9rem;
}
.discount-percent{
    color: green;
    margin-left: .5rem;
}

.product-card-more{
    font-size: .8rem;
}
.product-card-more > p{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}


/* wishlist */
.wishlist-symbols-container{
    position: absolute;
    top: .5rem;
    right: 2rem;
    z-index: 999;
}
.wishlist-symbols{
    position: relative;
    cursor: pointer;
}
.wishlist-symbols > .symbol{
    position: absolute;
}
.symbol:first-child{
    cursor: pointer;
}
.fill{
    display: none;
}
.wishlist-symbols:hover .fill{
    display: block;
}

/* product-image-carousel */
.product-img{
    flex: 0 0 var(--product-card-size);

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    width: var(--product-card-size);
    aspect-ratio: 1;
}
.product-img img{
    height: 100%;
    width: 100%;    
}

.product-card .image-carousel img{
    object-fit: cover !important;
    height: auto;
}

@media screen and (max-width:610px){
    
    .discount-contain{
        margin: 0!important;
        margin-left: .3rem !important;
    }
    
}

@media screen and (max-width:410px){
    :root{
        --product-card-size: 160px;
    }
}

/* Seller section  */
.seller-options-container{
    display: flex;
    justify-content: space-around;
    padding: .4rem 0;
}
.seller-options-container > *{
    padding: .2rem .3rem;
    border-radius: .1rem;
    -webkit-border-radius: .1rem;
    -moz-border-radius: .1rem;
    -ms-border-radius: .1rem;
    -o-border-radius: .1rem;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .2s;
    -webkit-transition: all .2s;
    -moz-transition: all .2s;
    -ms-transition: all .2s;
    -o-transition: all .2s;
}

.seller-options-container span{
    display: none;
    font-size: .8rem;
}
.icon{
    transform: translateY(0px);
    -webkit-transform: translateY(0px);
    -moz-transform: translateY(0px);
    -ms-transform: translateY(0px);
    -o-transform: translateY(0px);
}

.seller-options-container .option:hover{
    background-color: rgba(89, 88, 88, 0.12);
}
.seller-options-container .option:hover span{
    display: inline;
}
.seller-options-container .option:hover .icon{
    color: var(--primary);
}

/* remove tag */
.removing-tag{
    position: absolute;
    z-index: 99999;
    top: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 0, 0, .55);
    border-radius: .5rem;
    -webkit-border-radius: .3rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    font-size: .95rem;
    height: 100%;
    color: white;
    user-select: none;
    display: none;
}