:root{
    --primary: #357CF1;
    --border-light: #dfdfdfa4;
    --dot-color : rgb(52, 52, 52);
    --price-color: rgb(245, 29, 29);
    --product-name: #929292;
    --product-card-size: 200px;
    --seller-logo-color: brightness(50%) sepia(100) saturate(45) hue-rotate(218deg);
    --add-product-inputs: 450px;
}
html{
    scroll-behavior: smooth;
}
body{
    background-color: #F1F3F6;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
	-webkit-box-sizing: border-box !important;
	-moz-box-sizing: border-box !important;
	-ms-box-sizing: border-box !important;
	box-sizing: border-box !important;
    scrollbar-width: 0;
}
*::-webkit-scrollbar{
    display: none;
}
.login-button,.login-button-hard{
    line-height: 1.8rem;
    padding: 0 2.5rem;
    background-color: #fff;
    color: var(--primary);
    font-size: .9rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    cursor: pointer;
    border: none;
    border-radius: 1px;
    -webkit-border-radius: 1px;
    -moz-border-radius: 1px;
    -ms-border-radius: 1px;
    -o-border-radius: 1px;
}
.login-button-hard{
    background-color: var(--primary);
    color: #fff;
    line-height: 2rem;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    border: 1px solid transparent;
}

.login-button-hard:hover{
    border: 1px solid var(--primary);
    color: var(--primary);
    background-color: #fff;
}

.signup-link, .login-link, .btn-link{
    color: var(--primary);
    font-size: .9rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    cursor: pointer;
    text-decoration: none;
    padding-left: 15px;
}
.signup-link:hover, .login-link:hover, .btn-link:hover{
    text-decoration: underline;
}
.hard-btn{
    line-height: 2.5rem;
}    
.disable{
    background-color: grey;
    cursor: no-drop;
}
.disable:hover{
    background-color: grey;
    cursor: no-drop;
    color: #fff;
    border: none;
}
.disable-link{
    color: grey;
    cursor: no-drop;
}


.skip-btn{
    color: grey;
    font-size: .8rem;
    align-self: flex-end;
    letter-spacing: .05rem;
    margin: 0 1rem;
}
.close{
    position: absolute;
    top: .8rem;
    right: 1rem;
    color: grey;
    opacity: .5;
    cursor: pointer;
    z-index: 999999;
}

.back-btn{
    left: .5rem;
}


.entry-container{
    width: 600px;
    min-height: 270px;
    height: auto;
    padding-bottom: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #fff;
    box-shadow: 0 0 7px 1px rgba(0,0,0,0.5);
    z-index: 999;
    border-radius: 1px;
    -webkit-border-radius: 1px;
    -moz-border-radius: 1px;
    -ms-border-radius: 1px;
    -o-border-radius: 1px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    position: relative;
}

.icon{
    transform: translateY(4px);
    -webkit-transform: translateY(4px);
    -moz-transform: translateY(4px);
    -ms-transform: translateY(4px);
    -o-transform: translateY(4px);
}
.icon-down{
    transform: translateY(6px);
    -webkit-transform: translateY(6px);
    -moz-transform: translateY(6px);
    -ms-transform: translateY(6px);
    -o-transform: translateY(6px);
    margin-right: .5rem;
}
.results{
    display: flex;
    flex-direction: column;
    gap: .1rem;
}

.results > *{
    width: 100%;
    padding: .4rem 0;
    text-indent: 10px;
}

.results > *:last-child{
    border-bottom: none;
    margin-bottom: .5rem;
}
