
.title{
    text-align: center;
    display: block;
    padding-top: 1rem;
}

.input-container{
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: space-around;
    padding: .5rem 1.5rem 0;
    position: relative;
}
.input-container > label{
    font-size: 1.5rem;
    flex: 1.5;
    text-align: right;
    text-transform: capitalize;
}

.input-container > input, .input{
    flex: 2;
    line-height: 2.5rem;
    font-size: 1.5rem;
    padding: 0 1rem;
    border: none;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    box-shadow: 0 0 7px 1px rgba(0,0,0,0.3);
    outline: none;
    letter-spacing: 2px;
    margin-left: 2rem;
    margin-bottom: .8rem;
}
.input{
    background-color: transparent;
    box-shadow: none;
    padding-inline: 0;
}
#phone-read-only[readOnly]{
    filter: grayscale(1);
    box-shadow: 0 0 2px rgba(0,0,0,0.4);
    -webkit-filter: grayscale(1);
    font-family: monospace;
    font-size: 1.58rem;
    opacity: .45;
}

#phone-read-only[readOnly]:focus{
    box-shadow: 0 0 2px rgba(0,0,0,0.4) !important;
}

.input-container > input:focus{
    box-shadow: 0 0 5px 2px var(--primary);
}

.btn-contain{
    text-align: right;
    margin-top: 1.6rem;
    padding-right: 1.5rem;
}
.resend-otp{
    display: block;
    text-align: right;
    padding-right: 1.5rem;
    margin-top: .5rem;
}

form{
    display: flex;
    flex: 1;
    flex-direction: column;
}



.loader-container{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.13);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99999;
    display: none;
}

.loader-spinner{
    animation: rotate 0.8s ease infinite;
    -webkit-animation: rotate 0.8s ease infinite;
}
@keyframes rotate{
    0%{
        transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
}
    100%{
        transform: rotate(360deg);
        -webkit-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        -o-transform: rotate(360deg);
}   
}


@media screen and (max-width: 650px) {
    .entry-container{
        width: 95%;
        min-height: 300px;
        height: auto;
        padding: 1.6rem .4rem;
    }
    form{
        width: 100% !important;
    }
    .input-container{
        flex-direction: column;
        align-items: flex-start !important;
        align-self: center;
        /* width: 100%; */
        padding: .5rem 1rem 0;
    }
    .input-container > input{
        margin-left: 0 !important;
        padding: 0 .225rem!important;
        margin-bottom: .8rem !important;
        text-align: center;
        width: 100%;
    }

    .resend-otp{
        width: max-content;
        align-self: center;
    }
    
    .input-container > input::placeholder{
        text-align: center;
    }
    
    .input-container > label{
        position: absolute;
        margin-left: .5rem;
        margin-bottom: .8rem !important;
    }

    .btn-contain{
        text-align: center !important;
        padding-right: 0 !important;
    }

    .logout-btn{
        order: -1;
    }
}

