.container{
    width: 100%;
    height: 56px;
    display: flex;
    flex-direction: row;
    background-color: var(--primary);
    color: white;
    justify-content: space-evenly;
    align-items: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
}
.logo{
    flex-basis: 10%;
    text-align: right;
}
.logo img{
    width: 70px;
}
.search-bar{
    width: 30%;
    position: relative;
}
.search-bar #search{
    line-height: 2rem;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 0 15px 1px rgba(92, 92, 92, 0.5);
    border: none;
    outline: none;
    padding: 0 10px;
    border-radius: 1px;
    -webkit-border-radius: 1px;
    -moz-border-radius: 1px;
    -ms-border-radius: 1px;
    -o-border-radius: 1px;
}
.search-bar #search::placeholder{
    width: 90%;
}
.entry-contain{
    position: relative;
    min-width: 117px;
}
.profile-icon{
    transform: translateX(55px);
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    -webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    -ms-border-radius: 100%;
    -o-border-radius: 100%;
    background-color: brown;
    color: white;
    user-select: none;
    cursor: pointer;
    -webkit-transform: translateX(55px);
    -moz-transform: translateX(55px);
    -ms-transform: translateX(55px);
    -o-transform: translateX(55px);
    text-transform: uppercase;
}
.links{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.entry-contain:hover > .drop-down-container{
    display: block;
}
.humburger:hover > .drop-down-container{
    display: block;
}
/* Drop-down-menu design */
.drop-down-container{
    position: absolute;
    transform: translate(-25%);
    -webkit-transform: translate(-25%);
    -moz-transform: translate(-25%);
    -ms-transform: translate(-25%);
    -o-transform: translate(-25%);
    width: 200%;
    z-index: 9999;
    display: none;
}
:root{
    --arrow-height: 18px;
}
.seperator{
    height: var(--arrow-height);
}
.seperator-arrow{
    height: var(--arrow-height);
    aspect-ratio: 1;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    margin: auto;
    background-color: #fff;
}
.drop-down-menu-card{
    color: black;
    box-shadow: 0 0 10px 1px rgba(118, 118, 118, 0.45);
    border-radius: 1px;
    -webkit-border-radius: 1px;
    -moz-border-radius: 1px;
    -ms-border-radius: 1px;
    -o-border-radius: 1px;
    background-color: #fff;
}
#signup-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 20px 15px;
    border-bottom: 1px solid var(--border-light);
}
#signup-container p{
    font-size: .8rem;
    font-weight: 500;
}

.drop-options{
    display: block;
    padding: 10px 15px;
    border-bottom: 1px solid var(--border-light);
    font-size: .85rem;
    font-weight: 450;
    text-decoration: none;
    color: black;
    cursor: pointer;
}
.drop-options:last-child{
    border-bottom: none;
    padding-bottom: 1rem;
}
.drop-options.become-seller{
    display: none;
}

.drop-options:hover{
    background-color: rgb(247, 247, 247);
}

/* search-bar-bottom for small sevices */
.search-bar-container{
    background-color: var(--primary);
    padding: 7px 0;
    display: none;
    position: relative;
    width: 100%;
}
.search-bar-container input{
    outline: none;
    border: none;
    width: 95%;
    flex: 1;
    line-height: 2rem;
    margin: auto;
    display: block;
    padding: 0 5px;
    background-color: #fff;
}

.search-bar-container input::placeholder{
    width: 90%;
}
/* outlet */
.outlet-container{
    position: fixed;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 99999;
    background-color: rgba(18, 18, 18, 0.65);
    display: none;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(2px);
    overflow: scroll;
}

.drop-down-menu-card:has(.logout-btn){
    display: flex;
    flex-direction: column;
}
#signup-container:has(.logout-btn){
    display: block;
    order: 1;
}

@media screen and (max-width : 950px) {
    :root{
        --primary: orange;
    }
    .seller{
        display: none;
    }
    .drop-options.become-seller{
        display: block;
    }
    
    .container{
        background-color: orange;
    }
    
}

@media screen and (max-width: 650px) {
    :root{
        --primary: indianred;
    }
    .search-bar{
        visibility: hidden;
    }

    .container{
        background-color: indianred;
    }

    .search-bar-container{
        display: block;
    }
}

@media screen and (max-width: 570px){
    :root{
        --primary: violet;
    }
    .more{
        display: none;
    }    
    .add-cart p{
        display: none;
    }
    .container{
        justify-content: space-between;
        padding: 0 15px;
        background-color: violet;
    }

    .search-bar, .entry-contain, .more{
        display: none;
    }

    .humburger{
        display: block !important;
    }

    .drop-down-container{
        width: 200px;
        transform: none;
        -webkit-transform: none;
        -moz-transform: none;
        -ms-transform: none;
        -o-transform: none;

    }

}

